export default class XPopup {
    constructor(options = {}) {
        this.options = {
            selector: '',
            title: '',
            content: '',
            footer: '',
            timeOut: 0,
            show: true,
        }

        Object.assign(this.options, options);
        let body = document.querySelector('body');
        if (this.options.selector.length > 0) {
            this.popupEl = document.querySelector(this.options.selector);
        } else {
            this.popupEl = document.querySelector('#x-popup');
            if (!this.popupEl) {
                body.insertAdjacentHTML('beforeend', `<div id="x-popup" class="modal fade">
        <div class="modal-dialog popup modal-dialog-centered">
            <div class="modal-content">
                <div class="modal-header">
                    <h2 class="h3 mb-3 title text-center"></h2>
                    <button type="button" class="btn-close"></button>
                </div>
                <div class="modal-body">
                    
                </div>
                <div class="modal-footer d-none">
                </div>
            </div>
        </div>
    </div>`);
            }
            this.popupEl = document.querySelector('#x-popup');
            this.backdrop = document.querySelector('.modal-backdrop');
            this.popupEl.querySelector('.title').innerHTML = options.title;
            this.popupEl.querySelector('.modal-body').innerHTML = options.content;
            this.popupEl.querySelector('.modal-footer').innerHTML = this.options.footer;
            if (this.options.footer.length > 0) {
                this.popupEl.querySelector('.modal-footer').classList.remove('d-none');
            } else {
                this.popupEl.querySelector('.modal-footer').classList.add('d-none');
            }
        }
        this.backdrop = document.querySelector('.modal-backdrop');
        if (!this.backdrop) {
            body.insertAdjacentHTML('beforeend', `<div class="modal-backdrop fade d-none"></div>`);
        }

        this.popupEl.querySelector('.btn-close').addEventListener('click', () => this.hidePopupEl());
        window.addEventListener('click', (el) => {
            if (el.target === this.popupEl) {
                this.hidePopupEl();
            }
        });

        if (this.options.timeout) {
            setTimeout(() => this.hidePopupEl(),
                this.options.timeOut * 1000)
        }

        if (this.options.show) this.showPopupEl();
    }

    showPopupEl() {
        this.backdrop.classList.remove('d-none');
        this.popupEl.classList.add('d-block');
        setTimeout(() => {
            this.backdrop.classList.add('show');
            this.popupEl.classList.add('show');
            document.querySelector('body').classList.add('modal-open');
        }, 10);

    }

    hidePopupEl() {
        this.popupEl.classList.remove('show');
        this.backdrop.classList.remove('show');
        let t = setTimeout(() => {
            this.backdrop.classList.add('d-none');
            this.popupEl.classList.remove('d-block');
            document.querySelector('body').classList.remove('modal-open');
        }, 500);
    }
}