import "./main.scss";

import hljs from 'highlight.js';
import OwO from './module/OwO';
import vtoast from './vtoast';
import XPopup from './module/popup';
import XSidebarFixed from './module/sidebarFixed';
import XCatalog from './module/catalog';
import XAgree from './module/agree';
import axios from "axios";
import LoopSlider from './module/slider';
window.$ = window.jQuery = require('jquery-slim');


class XGo {
    constructor() {
        this.options = null;
        this.headerHeight = 55;
        this.vtoast = vtoast;
        if ($('.breadcrumbs').length > 0) {
            this.headerHeight = $('.navbar').offset().height;
        } else {
            this.headerHeight = $('.header').offset().height;
        }
        this.options = window.tConf.options;
        this.text = window.tConf.i18n;
        this.initScroll();
        this.initHighLight();
        this.initEvent();
        this.initNavbar();
        this.initSwiper();
        this.initPhotoSwipe();
        this.initOwO();
        this.initLazyload();
    }

    _themeUrl(uri = '') {
        const start = new RegExp(/^\//, 'g');
        const end = new RegExp(/\/$/, 'g');
        const baseUrl = this.options.themeUrl.replace(end, "") + "/";
        return (baseUrl + uri.replace(start, ""));
    }

    scrollTo(selector, duration = 200, offset = 0) {
        if ($(selector).length) {
            window.scrollTo(scrollX, $(selector).offset().top + offset);
        }
    }

    initLazyload(images = null) {
        require('lazyload');
        if (!images)
            images = $('img.lazy');
        images.on('error', function () {
            $(this).prop('outerHTML', `<div class="error-loading"><svg class="icon" viewBox="0 0 1024 1024"><path d="M512 938.666667c235.648 0 426.666667-191.018667 426.666667-426.666667S747.648 85.333333 512 85.333333 85.333333 276.352 85.333333 512s191.018667 426.666667 426.666667 426.666667z m-38.912-643.242667h77.824v305.152h-77.824V295.424zM473.6 716.8V640h76.8v76.8h-76.8z"></path></svg></div>`);
        })
        images.on('load', function () {
            $(this).removeClass('lazy');
        })
        lazyload(images);
    }

    initEvent() {
        let _this = this;
        // 点击显示搜索框
        $('.action-search').on('click', el => {
            el.preventDefault();
            $('.sidebar-collapse').toggleClass('active');
            $('body').toggleClass('active');
        });

        // 点击显示移动端导航
        $('.action-menu').on('click', el => {
            el.preventDefault();
            $('.mobile-navbar').toggleClass('active');
            $('body').toggleClass('active');
        });

        // 点击关闭搜索框，移动端导航
        $('.bg-overlay').on('click', el => {
            el.preventDefault();
            $('.mobile-navbar').removeClass('active');
            $('.sidebar-collapse').removeClass('active');
            $('body').removeClass('active');
        });

        // 模态框
        $('[data-modal]').each(function () {
            let modal = new XPopup({selector: $(this).data('modal'), show: false})
            $(this).on('click', () => modal.showPopupEl());
        });

        // 微信二维码
        if ($('.single-popup.weixin').length > 0) {
            $('.single-popup.weixin').on('click', el => {
                el.preventDefault();
                let dom = $(el.target);
                let html = '<div class="text-center">' +
                    '<img src="' + dom.data('img') + '" alt="' + dom.data('title') + '" style="width:100%;height:auto;">' +
                    '<p class="text-muted text-xs">' + dom.data('desc') + '</p></div>';
                new XPopup({title: dom.data('title'), content: html});
            });
        }

        // 跳转到评论
        $('#x-go-to-comments') && $('#x-go-to-comments').on('click', () => {
            this.scrollTo('#comments', 200, -20);
        });

        // 移动端菜单
        $('.mobile-menu .menu-has-children svg').on('click', event => {
            event.preventDefault();
            event.stopPropagation();
            let subMenu = $(event.target).closest('.menu-has-children').find('> .sub-menu');
            subMenu.toggleClass('d-block');
            return false;
        });

        // 搜索
        $('.search-input input').on('input', function (e) {
            const $input = $(this);
            let query = $input.val();
            if (query.length > 0) {
                console.log(_this.options.ajax.searchUrl + '/' + encodeURI(query) + '?type=instant');
                axios({
                    url: _this.options.ajax.searchUrl + '/' + encodeURI(query) + '?type=instant',
                }).then(response => {
                    let ulNode = $(".search-result ul");
                    let loadMoreNode = $(".search-result .loadmore");
                    let html = '';
                    $.each(response.data, function (i, element) {
                        html += '<li class="border-bottom py-2 text-sm"><a href="' + element.permalink + '">' + element.title + '</a></li>';
                    });
                    ulNode.html(html);
                    let items = ulNode.find("li") || [];
                    $(".widget-search .count").html('(' + items.length + ')');
                    if (items.length < 5) {
                        loadMoreNode.hide()
                    } else {
                        loadMoreNode.show()
                        loadMoreNode.find('a').attr('href', _this.options.ajax.searchUrl + '/' + encodeURI(query));
                    }
                });
            }
            return false;
        });

        // 点赞
        new XAgree({
            success: () => {
                _this.vtoast.show('', window.tConf.i18n.upvote.thankYou, {
                    duration: 3000,
                    color: '#0f5132',
                    backgroundcolor: '#d1e7dd'
                });
            },
            already: () => {
                _this.vtoast.show('', window.tConf.i18n.upvote.already, {
                    duration: 3000,
                    color: '#842029',
                    backgroundcolor: '#f5c2c7'
                });
            }
        });

        // 私密评论
        $('#secret-comment').on('click', el => {
            if (el.target.checked) {
                $('#comment').addClass('secret');
            } else {
                $('#comment').removeClass('secret');
            }
        })
    }

    initScroll() {
        const scrollToTopBtn = $("#x-back-to-top");
        this.sidebarFixed = new XSidebarFixed({selector: '.stickySidebar', offsetTop: $('header').outerHeight() + 20, type: 'sticky'});
        this.catalog = new XCatalog({offsetHeight: $('header').outerHeight()});

        // 点击回到顶部
        scrollToTopBtn.on('click', () => {
            window.scrollTo(scrollX, 0);
        });

        // 滚动事件
        $(window).on('scroll', () => {
            let scrollableHeight = document.documentElement.scrollHeight - document.documentElement.clientHeight,
                GOLDEN_RATIO = 0.3;

            if ((document.documentElement.scrollTop / scrollableHeight) > GOLDEN_RATIO) {
                scrollToTopBtn.addClass('active');
            } else {
                scrollToTopBtn.removeClass('active');
            }
        });
    }

    initHighLight() {
        $('pre code').each(function (i, el) {
            $(el).attr('class', '');
            hljs.highlightElement(el);
            $(el).html("<ul><li>" + $(el).html().replace(/\n/g, "\n</li><li>") + "\n</li></ul>");
        });
    }

    initPhotoSwipe() {
        this.lightBox = require('fslightbox');
    }

    initNavbar() {
        let _this = this,
            collapseWidth = $('.navbar-collapse').outerWidth(true),
            mainMenu = $('.main-menu'),
            hiddenMenus = [], i = 0;
        while (i++ < 100 && collapseWidth < mainMenu.width() + $('> li:last-child', mainMenu).width()) {
            hiddenMenus.push($('> li:last-child', mainMenu).remove());
        }
        if (hiddenMenus.length > 0) {
            let moreMenu = $('<li/>'),
                subMenu = $('<ul>').attr('class', 'sub-menu default');
            moreMenu.attr('class', 'menu-item more sub-item');
            moreMenu.append('<a href="#" class="s-1x">' + window.tConf.i18n.more + '<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 32 32"><path d="M 16 6 C 14.894531 6 14 6.894531 14 8 C 14 9.105469 14.894531 10 16 10 C 17.105469 10 18 9.105469 18 8 C 18 6.894531 17.105469 6 16 6 Z M 16 14 C 14.894531 14 14 14.894531 14 16 C 14 17.105469 14.894531 18 16 18 C 17.105469 18 18 17.105469 18 16 C 18 14.894531 17.105469 14 16 14 Z M 16 22 C 14.894531 22 14 22.894531 14 24 C 14 25.105469 14.894531 26 16 26 C 17.105469 26 18 25.105469 18 24 C 18 22.894531 17.105469 22 16 22 Z"/></svg></a>');
            $.each(hiddenMenus, function (i, v) {
                subMenu.prepend(v);
            });
            moreMenu.append(subMenu);
            mainMenu.append(moreMenu);
        }
        mainMenu.removeClass('resize');
    }

    initOwO() {
        let _this = this;
        if ($('#OwO_Container').length > 0) {
            if (typeof module !== 'undefined' && typeof module.exports !== 'undefined') {
                module.exports = OwO;
            } else {
                window.OwO = OwO;
            }
            const reToLazy = new RegExp("src=", "g");
            const reMirror = new RegExp("{imageMirror}", "g");

            new OwO({
                api: window.tConf.options.comments.emojiApi,
                position: 'down',
                width: '100%',
                maxHeight: '250px',
                imageHandler: (icon) => {
                    if (window.tConf.options.lazyload.isEnabled) {
                        icon = icon.replace(reToLazy, `src="${window.tConf.options.lazyload.image}" data-src=`);
                    }
                    return icon.replace(reMirror, window.tConf.options.OwO.imageMirror);
                },
                logoClick: (el, container) => {
                    if (!el.target.classList.contains('loaded')) {
                        _this.initLazyload($('img.lazy', $(container)));
                        el.target.classList.add('loaded')
                    }
                }
            });

        }
    }

    initSwiper() {
        if ($('.slider-wrapper').length) {
            new LoopSlider({
                el: ".slider-wrapper", //选择器
                navigator: {
                    //前进后退按钮
                    prevEl: ".slide-prev",
                    nextEl: ".slide-next"
                },
                easing: "ease", //动画效果cubic-bezier(0.985, -0.060, 0.000, 1.320)
                duration: 300, //过渡时间，默认为3000ms,
                autoplay: 3000, //boolean 和 number 设置为true 默认延时为3s,如果设置为1000，延时为1s
            })
        }
    }
}

document.addEventListener('DOMContentLoaded', () => {
    window.XGo = new XGo();
});