export default class OwO {
    log(text, type = 'notice') {
        if (type == 'error') {
            console.log('%c ' + text, 'color: #fff; background: red')
        } else {
            console.log(text, 'color: #fff; background: red')
        }
    }

    constructor(options) {
        this.options = {
            logo: document.querySelector('.OwO-logo'),
            container: document.querySelector('#OwO_Container'),
            target: document.querySelector('.OwO-textarea'),
            position: 'down',
            width: '100%',
            maxHeight: '250px',
            imageHandler: (icon) => {
                return icon;
            },
            logoClick: () => {
            }
        }
        Object.assign(this.options, options)
        if (!this.options.hasOwnProperty('api')) {
            this.log('Api not provided', 'error');
        }
        this.container = this.options.container;
        this.target = this.options.target;
        if (this.options.position === 'up') {
            this.container.classList.add('OwO-up');
        }

        const xhr = new XMLHttpRequest();
        xhr.onreadystatechange = () => {
            if (xhr.readyState === 4) {
                if (xhr.status >= 200 && xhr.status < 300 || xhr.status === 304) {
                    this.odata = JSON.parse(xhr.responseText);
                    this.init(this.options);
                } else {
                    this.log('OwO data request was unsuccessful: ' + xhr.status, 'error');
                }
            }
        };
        xhr.open('get', this.options.api, true);
        xhr.send(null);
    }

    init(option) {
        this.area = option.target;
        this.packages = Object.keys(this.odata);

        let logo = `<div class="OwO-logo"><span>${option.logo}</span></div>`;
        if (option.logo) {
            logo = '';
        }

        // fill in HTML
        let html = `
            ${logo}
            <div class="OwO-body" style="width: ${option.width}">`;

        for (let i = 0; i < this.packages.length; i++) {

            html += `
                <ul class="OwO-items OwO-items-${this.odata[this.packages[i]].type}" style="max-height: ${parseInt(option.maxHeight) - 53 + 'px'};">`;

            let opackage = this.odata[this.packages[i]].container;
            for (let i = 0; i < opackage.length; i++) {
                let icon = option.imageHandler(opackage[i].icon);
                html += `
                    <li class="OwO-item" title="${opackage[i].text}" data-text="${opackage[i].data}">${icon}</li>`;

            }

            html += `
                </ul>`;
        }

        html += `
                <div class="OwO-bar">
                    <ul class="OwO-packages">`;

        for (let i = 0; i < this.packages.length; i++) {

            html += `
                        <li><span>${this.packages[i]}</span></li>`

        }

        html += `
                    </ul>
                </div>
            </div>
            `;
        this.container.innerHTML = html;


        // bind event
        this.logo = option.logo;
        this.logo.addEventListener('click', el => {
            el.preventDefault();
            if (option.logoClick) {
                option.logoClick(el, this.container);
            }
            this.toggle();
        });

        this.container.getElementsByClassName('OwO-body')[0].addEventListener('click', (e) => {
            let target = null;
            if (e.target.classList.contains('OwO-item')) {
                target = e.target;
            } else if (e.target.parentNode.classList.contains('OwO-item')) {
                target = e.target.parentNode;
            }
            if (target) {
                const cursorPos = this.area.selectionEnd;
                let areaValue = this.area.value;
                this.area.value = areaValue.slice(0, cursorPos) + target.dataset.text + areaValue.slice(cursorPos);
                this.area.focus();
                this.toggle();
            }
        });

        document.addEventListener('click', el => {
            if (!this.container.contains(el.target) && el.target !== this.logo && !this.logo.contains(el.target) && this.container.classList.contains('OwO-open')) {
                this.container.classList.remove('OwO-open');
            }
        });

        this.packagesEle = this.container.getElementsByClassName('OwO-packages')[0];
        for (let i = 0; i < this.packagesEle.children.length; i++) {
            ((index) => {
                this.packagesEle.children[i].addEventListener('click', () => {
                    this.tab(index);
                });
            })(i);
        }

        this.tab(0);
    }

    toggle() {
        if (this.container.classList.contains('OwO-open')) {
            this.container.classList.remove('OwO-open');
        } else {
            this.container.classList.add('OwO-open');
        }
    }

    tab(index) {
        const itemsShow = this.container.getElementsByClassName('OwO-items-show')[0];
        if (itemsShow) {
            itemsShow.classList.remove('OwO-items-show');
        }
        this.container.getElementsByClassName('OwO-items')[index].classList.add('OwO-items-show');

        const packageActive = this.container.getElementsByClassName('OwO-package-active')[0];
        if (packageActive) {
            packageActive.classList.remove('OwO-package-active');
        }
        this.packagesEle.getElementsByTagName('li')[index].classList.add('OwO-package-active');
    }
}