import axios from "axios";

export default class XAgree {
    constructor(options = {}) {
        this.options = {
            selector: '#post-like',
            countSelector: '.like-count',
            success: () => {
            },
            already: () => {
            }
        }
        Object.assign(this.options, options);
        if ($(this.options.selector).length && $(this.options.selector)) {
            this.init();
        }
    }

    init() {
        this.button = $(this.options.selector);
        if (!this.options.hasOwnProperty('url')) {
            this.options.url = location.href.split("?")[0] + "?themeAction=promo";
        }
        let _this = this;
        let params = new URLSearchParams();
        params.append('operate', 'trigger');
        params.append('field', 'agree');
        this.button.on('click', function () {
            if ($(this).prop('disabled')) {
                _this.options.already();
            }
            axios({
                method: 'post',
                url: _this.options.url,
                data: params,
            }).then((response) => {
                _this.button.prop('disabled', true);
                if (response.data.hasOwnProperty('data') && response.data.data.hasOwnProperty('value')) {
                    _this.button.find(_this.options.countSelector).text('(' + response.data.data.value + ')');
                    _this.options.success();
                }
            });
        });
    }
}