import $ from "jquery-slim";

export default class XSidebarFixed {
    constructor(options = {}) {
        this.options = {
            selector: '.stickySidebar',
            offsetTop: 0,
            type: 'marginTop'
        }
        Object.assign(this.options, options);
        this.init();
    }

    init() {
        this.sidebarElements = $(this.options.selector);
        if (this.sidebarElements.length === 0) {
            return;
        }
        let _this = this;
        if (_this.options.type == 'marginTop') {
            this.sidebarElements.each(function () {
                let el = $(this);
                el.attr('data-offset', el.offset().top);
                _this.handler(el);
            })
            this.bindEvents();
        }
        if (_this.options.type == 'sticky') {
            this.sidebarElements.each(function () {
                let el = $(this);
                el.css({position: 'sticky', top: _this.options.offsetTop});
            })
        }
    }

    bindEvents() {
        let _this = this;
        this.sidebarElements.each(function () {
            let el = $(this);
            $(window).on('scroll', function () {
                _this.handler(el);
            });
        });
    }

    handler(item) {
        let sbOffset = item.data('offset');
        if (scrollY > sbOffset) {
            item.css({marginTop: scrollY + this.options.offsetTop - sbOffset + 'px'});
        } else {
            item.css({marginTop: 0});
        }
    }
}