import $ from "jquery-slim";

export default class LoopSlider {

    initVal() {
        this.options = {
            selector: "",
        }
        this.currentIndex = 0;
        this.movement = 0;
        this.selector = "";
        this.max = 0;
        this.delay = 800;
        this.duration = 300;
        this.easing = "ease";
        this.time = null;
        this.id = null;
        this.perIndex = 0;
    }
    constructor(config) {
        this.initVal();
        let _t = this;
        _t.options.selector = config.el;
        $(function () {
            if (config.easing != null)
                _t.easing = config.easing;
            _t.duration = config.duration;
            _t.delay = config.autoplay;
            if (_t.delay || _t.delay <= 0) {
                _t.autoplay();
            }
            const slides = $(config.el).children();
            _t.movement = $(config.el).width();
            _t.max = slides.length;
            slides.wrap("<div class='slide-frame'></div>");
            const firstItemClone = $(".slide-frame:first").clone();
            firstItemClone.insertAfter($(".slide-frame:last"));
            $(".slide-frame").wrapAll("<div class='slider-bar'></div>");
            $(".slider-bar").wrap("<div class='slider-shade'></div>")
            $(".slider-bar").css({
                overflow: "hidden",
                position: "absolute",
                left: 0,
                height: "100%",
                width: `${_t.movement * (slides.length + 2)}px`
            });
            $(".slide-frame").css({
                width: `${_t.movement}px`,
                overflow: "hidden",
                height: "100%",
                float: "left"
            });

            $(config.el).append("<div class='" + config.navigator.nextEl.replace(".", "") + "'> > </div>");
            $(config.el).append("<div class='" + config.navigator.prevEl.replace(".", "") + "'> < </div>")
            $(config.el).append("<div class='slide-navigation'></div>");
            for (let i = 0; i < slides.length; i++) {
                if (i == 0) {
                    $(".slide-navigation").append("<li class='active'></li>");
                } else {
                    $(".slide-navigation").append("<li></li>");
                }
            }
            _t.options.selector = $(".slider-bar");

            function EventListen() {
                $(config.navigator.nextEl).click(function () {
                    _t.next(_t);
                });
                $(config.navigator.prevEl).click(function () {
                    _t.prev(_t)
                });
            }

            EventListen();
            _t.itemClick(_t);
        });
        $(window).resize(function () {
            _t.movement = $(config.el).width();
            $(".slider-bar").css({
                transition: "all 0s"
            });
            $(".slider-bar").css({
                width: `${_t.movement * (_t.max + 2)}px`
            });
            $(".slide-frame").css({
                width: `${_t.movement}px`,
            });
        });
    }

    autoplay() {
        let _t = this;
        let play = function () {
            _t.time = setTimeout(function () {
                _t.id = requestAnimationFrame(play);
                _t.next(_t);
            }, _t.delay);
        };
        $(_t.options.selector).hover(function () {
            clearTimeout(_t.time);
            cancelAnimationFrame(_t.id);
        }, function () {
            play();
        });
        play();
    }

    prev(t) {
        t.currentIndex--;
        if (t.currentIndex < 0) {
            t.backCall(true, t);
            t.currentIndex = t.max - 1;
        } else {
            t.slideGoTo(t.currentIndex, t)
        }
        t.aimPoint(t.currentIndex, false);
    }

    next(t) {
        t.currentIndex++;
        let pageIndex = t.currentIndex;
        if (t.currentIndex > t.max) {
            t.backCall(false, t);
            t.currentIndex = 1;
        } else {
            t.slideGoTo(t.currentIndex, t)
        }
        t.aimPoint(t.currentIndex, true);
    }

    backCall(os, _this) {
        if (os) {
            _this.options.selector.css({
                transform: `translate3d(-${_this.max * _this.movement}px,0px,0px)`,
                transition: "all 0s"
            });
            _this.slideTo(_this, (_this.max - 1) * _this.movement)
        } else {
            //Right
            _this.options.selector.css({
                transform: `translate3d(0px,0px,0px)`,
                transition: "all 0s"
            });
            _this.slideTo(_this, _this.movement);
        }
    }

    slideTo(_this, backposition) {
        requestAnimationFrame(() => {
            requestAnimationFrame(() => {
                _this.options.selector.css({
                    transform: `translate3d(-${backposition}px,0px,0px)`,
                    transition: `all ${_this.duration}ms ${_this.easing}`
                });
            })
        })
    }

    aimPoint(index, bool) {
        if (bool) {
            this.perIndex += 1;
        } else {
            this.perIndex -= 1;
        }
        if (this.perIndex >= this.max) {
            this.perIndex = 0;
        } else if (this.perIndex < 0) {
            this.perIndex = this.max - 1;
        }
        this.activeItem(this.perIndex);
    }

    activeItem(index) {
        $(".slide-navigation li").eq(index).addClass("active").siblings("li").removeClass("active");
    }

    itemClick(t) {
        $(".slide-navigation li").click(function () {
            t.slideGoTo($(this).index(), t);
        });
    }

    slideGoTo(index, _t) {
        _t.options.selector.css({
            transform: `translate3d(-${index * _t.movement}px,0px,0px)`,
            transition: `all ${_t.duration}ms ${_t.easing}`
        });
        _t.activeItem(index);
    }
}