import $ from "jquery-slim";

export default class XCatalog {
    constructor(options = {}) {
        this.options = {
            selector: '.x-catalog',
            hrefAttribute: 'data-href',
            offsetHeight: '100',
            scrollFollow: true
        }
        Object.assign(this.options, options);
        if ($(this.options.selector).length) {
            this.init();
        }
    }

    init() {
        this.catalog = $(this.options.selector);
        this.initHeight();
        this.initClickToGo();
        this.initScrollFollow();
    }

    initHeight() {
        let catHeight = this.catalog.height(),
            maxHeight = $(window).height() - this.options.offsetHeight - 80;
        this.catalog.css({'maxHeight': maxHeight + 'px'});
        if (catHeight > maxHeight) {
            this.catalog.css({'overflowY': 'scroll'});
        }
    }

    initClickToGo() {
        let offsetHeight = this.options.offsetHeight;
        $('a[' + this.options.hrefAttribute + ']', this.catalog).each(function () {
            $(this).on('click', event => {
                event.preventDefault();
                window.scrollTo(0, $(decodeURI(event.target.getAttribute('data-href'))).offset().top - offsetHeight);
                return false;
            })
        });
    }

    initScrollFollow() {
        if (this.options.scrollFollow) {
            let options = this.options;
            let catalogLinks = $('a', this.catalog);
            $(window).on('scroll', function () {
                if (catalogLinks.length) {
                    let fromTop = scrollY + options.offsetHeight + 10;
                    for (let i = 0; i < catalogLinks.length; i++) {
                        let link = catalogLinks.eq(i),
                            section = $(decodeURI($(link).data('href'))),
                            nextSection = null;

                        if (catalogLinks.eq(i + 1)) {
                            nextSection = $(decodeURI($(catalogLinks.eq(i + 1)).data('href')));
                        }

                        if (section.length > 0 && section.offset().top <= fromTop) {
                            if (nextSection.length > 0) {
                                if (nextSection.offset().top > fromTop) {
                                    link.addClass("current");
                                } else {
                                    link.removeClass("current");
                                }
                            } else {
                                link.addClass("current");
                            }
                        } else {
                            link.removeClass("current");
                        }
                    }
                }
            });
        }
    }
}